.list {
    color: aliceblue;
    width: 300px;
    background-color: cornflowerblue;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p{
    padding: 10px;
    position: relative;

}

.list p span {
    right: 10px;
    position: absolute;
    cursor: pointer;
}

.faicons {
    margin: 0 5px;
    font-size: 15px;
}

.list p input {
    background-color: transparent;
    border: 0;
    color: aliceblue;
    font-size: 18px;

}

.list p input ::focus{
    outline: none;
}